@import url('https://fonts.googleapis.com/css?family=Playfair+Display&display=swap');
@font-face {
	font-family: 'Playfair Display';
	src: url('https://fonts.googleapis.com/css?family=Playfair+Display&display=swap');
}

* {
	box-sizing: border-box;
	-webkit-tap-highlight-color: transparent;
	-webkit-user-select: none;
	-ms-user-select: none;
	-moz-user-select: none;
	-o-user-select: none;
	user-select: none;
}

html {
	font-size: 100%;
}

body {
	width: 100%;
	height: 100%;
	margin: 0;
	padding: 0;
	text-align: left;
	font-family: 'Playfair Display', 'Times New Roman', serif;
}

.wrapper {
	background-color: #f5f5f5;
	color: #212121;
	width: 100%;
	height: 100%;
	min-height: 100vh;
	margin: 0;
	padding: 0;
}

header.primary {
	width: 100%;
	margin: 0 auto;
	padding: 0.5em 6em;
	position: fixed;
	display: none;
	-webkit-user-select: none;
	-ms-user-select: none;
	-moz-user-select: none;
	-o-user-select: none;
	user-select: none;
}

header.primary nav {
	display: grid;
	grid-template-areas: 'heading nav-buttons';
}

header.primary span.heading {
	font-size: 1.5rem;
	margin: auto 1em;
	grid-area: heading;
	justify-self: start;
}

.nav-buttons {
	margin: auto 0em;
	list-style: none;
	grid-area: nav-buttons;
	justify-self: end;
}

.nav-buttons li {
	display: inline-block;
	margin: 0.5em 1em;
}

.nav-buttons li a.nav-link {
	color: #212121;
	opacity: 0.75;
	font-size: 1rem;
	-webkit-transition: 0.3s ease-in-out;
	-ms-transition: 0.3s ease-in-out;
	-moz-transition: 0.3s ease-in-out;
	-o-transition: 0.3s ease-in-out;
	transition: 0.3s ease-in-out;
}

.nav-buttons li a.nav-link:hover {
	color: #000000;
	opacity: 1;
}

button.keep-login-facebook, button.metro {
	border: none;
	border-radius: 0.2em;
	color: #ffffff;
	width: 100%;
	font-family: 'Playfair Display';
	font-size: 1rem;
	text-transform: none;
	font-weight: 100;
	padding: 0.5em 1.5em;
	-webkit-transition: 0.3s ease-in-out;
	-ms-transition: 0.3s ease-in-out;
	-moz-transition: 0.3s ease-in-out;
	-o-transition: 0.3s ease-in-out;
	transition: 0.3s ease-in-out;
}

button.keep-login-facebook:hover, button.metro:hover {
	background-color: #254dba;
}

a {
	text-decoration: none;
}

a.button {
	background-color: #f50057;
	color: #ffffff;
	font-size: 1rem;
	padding: 0.5em 1.5em;
	cursor: pointer;
	-webkit-transition: 0.3s ease-in-out;
	-ms-transition: 0.3s ease-in-out;
	-moz-transition: 0.3s ease-in-out;
	-o-transition: 0.3s ease-in-out;
	transition: 0.3s ease-in-out;
}

a.button:hover {
	background-color: #9e0036;
}

a.round-button {
	box-shadow: 4px 4px 1px 1px rgba(0, 0, 0, 0.3);
	background-color: #f50057;
	border: 0;
	border-radius: 100%;
	color: #ffffff;
	width: 48px;
	height: 48px;
	font-size: 32px;
	font-weight: 1000;
	cursor: pointer;
	position: fixed;
	right: 0.5em;
	bottom: 0.5em;
	vertical-align: bottom;
	-webkit-transition: 0.3s ease-in-out;
	-ms-transition: 0.3s ease-in-out;
	-moz-transition: 0.3s ease-in-out;
	-o-transition: 0.3s ease-in-out;
	transition: 0.3s ease-in-out;
}

a.round-button:hover {
	box-shadow: 5px 5px 1px 1px rgba(0, 0, 0, 0.2);
	transform: scale(1.1);
}

a.round-button.active {
	box-shadow: 0px 0px rgba(0, 0, 0, 0.3);
	transform: scale(1.1);
	transform: rotate(-225deg);
}

main.primary {
	height: 100%;
	/*padding-top: 5em;*/
}

main.primary article {
	text-align: center;
}

main.primary span.heading {
	display: block;
	font-size: 2rem;
}

main.primary span.subheading {
	display: block;
	font-size: 1.5rem;
}

main.primary div.content {
	display: block;
	line-height: 1.5;
}

.force-login-container {
	padding: 4em;
}

.force-login-container div {
	margin: 1em auto;
}

.force-login-button-container {
	width: 15rem;
}

.force-login-container a.button {
	display: inline-block;
	width: 100%;
	margin: 0em auto;
}

.editing-container {
	width: 100%;
	height: 75vh;
	position: fixed;
	top: 0;
	-webkit-transition: 0.3s ease-in-out;
	-ms-transition: 0.3s ease-in-out;
	-moz-transition: 0.3s ease-in-out;
	-o-transition: 0.3s ease-in-out;
	transition: 0.3s ease-in-out;
}

.editing-container.hide {
	top: -100%;
}

.art-options {
	box-shadow: 4px 4px rgba(0, 0, 0, 0.3);
	background-color: #f50057;
	color: #ffffff;
	opacity: 1;
	visibility: visible;
	padding: 0em 4em 0em 0em;
	text-align: left;
	position: fixed;
	display: block;
	right: 0.5em;
	bottom: 0.5em;
	-webkit-transition: 0.3s ease-in-out;
	-ms-transition: 0.3s ease-in-out;
	-moz-transition: 0.3s ease-in-out;
	-o-transition: 0.3s ease-in-out;
	transition: 0.3s ease-in-out;
}

.art-options.hide {
	opacity: 0;
	visibility: hidden;
}

.art-options a.art-option-button {
	background-color: #f50057;
	color: #ffffff;
	padding: 0.5em 1em;
	display: block;
	-webkit-transition: 0.3s ease-in-out;
	-ms-transition: 0.3s ease-in-out;
	-moz-transition: 0.3s ease-in-out;
	-o-transition: 0.3s ease-in-out;
	transition: 0.3s ease-in-out;
}

.art-options a.art-option-button:hover {
	background-color: #9e0036;
}

.art-options span {
	opacity: 0.7;
	padding: 0em 1em;
}

.close-window-button {
	color: #ffffff;
	width: 72px;
	height: 72px;
	font-size: 48px;
	font-weight: 1000;
	cursor: pointer;
	position: absolute;
	top: 0.5rem;
	right: 0.5rem;
	vertical-align: middle;
	transform: rotate(45deg);
	-webkit-transition: 0.3s ease-in-out;
	-ms-transition: 0.3s ease-in-out;
	-moz-transition: 0.3s ease-in-out;
	-o-transition: 0.3s ease-in-out;
	transition: 0.3s ease-in-out;
}

.close-window-button:hover {
	transform: rotate(225deg);
}

.doodle-canvas-container, .editing-image-container, .image-to-add-container {
	box-shadow: 5px 5px 3px 1px rgba(0, 0, 0, 0.3);
	background-color: #212121;
	color: #f5f5f5;
	width: 70%;
	min-height: 60%;
	position: absolute;
	top: 2em;
	left: 0;
	right: 0;
	margin: auto;
	display: inline-block;
	touch-action: none;
	-webkit-transition: 0.3s ease-in-out;
	-ms-transition: 0.3s ease-in-out;
	-moz-transition: 0.3s ease-in-out;
	-o-transition: 0.3s ease-in-out;
	transition: 0.3s ease-in-out;
}

.doodle-canvas-container {
	background-color: #ffffff;
	-webkit-transition: none;
	-ms-transition: none;
	-moz-transition: none;
	-o-transition: none;
	transition: none;
}

.doodle-canvas-container span.subheading, .editing-image-container span.subheading {
	padding-top: 0.5em;
	background-color: #212121;
}

.editing-image-container {
	padding: 0em 1em 1em 0em;
}

.doodle-canvas {
	cursor: crosshair;
}

.tool-panel {
	background-color: #212121;
	text-align: center;
}

.settings-panel {
	background-color: #212121;
	padding-bottom: 0.5em;
}

.tool-panel-button {
	background-color: #212121;
	color: #f5f5f5;
	opacity: 0.6;
	height: 2.5em;
	cursor: pointer;
	display: inline-block;
	padding: 0.5em;
	font-size: 1rem;
	vertical-align: top;
	-webkit-transition: 0.3s ease-in-out;
	-ms-transition: 0.3s ease-in-out;
	-moz-transition: 0.3s ease-in-out;
	-o-transition: 0.3s ease-in-out;
	transition: 0.3s ease-in-out;
}

.tool-panel-button:hover, .tool-panel-button.active {
	background-color: #424242;
	color: #ffffff;
	opacity: 1;
	cursor: pointer;
}

.tool-panel-button svg.icon {
	fill: rgba(255, 255, 255, 0.7);
	-webkit-transition: 0.3s ease-in-out;
	-ms-transition: 0.3s ease-in-out;
	-moz-transition: 0.3s ease-in-out;
	-o-transition: 0.3s ease-in-out;
	transition: 0.3s ease-in-out;
}

.tool-panel-button:hover svg.icon, .tool-panel-button.active svg.icon {
	fill: rgba(255, 255, 255, 1);
}

.tool-panel-button svg.shape {
	stroke: rgba(255, 255, 255, 0.7);
	stroke-width: 3;
	fill: none;
	-webkit-transition: 0.3s ease-in-out;
	-ms-transition: 0.3s ease-in-out;
	-moz-transition: 0.3s ease-in-out;
	-o-transition: 0.3s ease-in-out;
	transition: 0.3s ease-in-out;
}

.tool-panel-button:hover svg.shape, .tool-panel-button.active svg.shape {
	stroke: rgba(255, 255, 255, 1);
}

.color-select-container, .size-select-container {
	display: inline-block;
	cursor: pointer;
}

.color-select-head, .size-select-head {
	background-color: #ffffff;
	width: 0.75rem;
	height: 0.75rem;
	cursor: pointer;
	display: inline-block;
	margin: 0rem 0.875rem;
	font-size: 1.2rem;
}

.color-select-options, .size-select-options {
	background-color: #212121;
	padding: 0.5em;
	position: fixed;
	max-height: 100vh;
	overflow-y: hidden;
	-webkit-transition: 0.3s ease-in-out;
	-ms-transition: 0.3s ease-in-out;
	-moz-transition: 0.3s ease-in-out;
	-o-transition: 0.3s ease-in-out;
	transition: 0.3s ease-in-out;
}

.color-select-options.hide, .size-select-options.hide {
	max-height: 0px;
	padding: 0 0.5em;
}

.color-select-options .color-select-box, .size-select-options .size-select-box {
	border: 2px solid #212121;
	background-color: #ffffff;
	width: 1.5rem;
	height: 1.5rem;
	display: block;
	cursor: pointer;
	font-size: 1.2rem;
}

.size-select-head, .size-select-options .size-select-box {
	background-color: #212121;
}

.tool-panel .image-selector {
	position: absolute;
	width: 100%;
	padding: 1em;
}

.image-selector {
	background-color: #212121;
	color: #ffffff;
	max-height: 100vh;
	overflow-y: hidden;
	visibility: visible;
	-webkit-transition: 0.3s ease-in-out;
	-ms-transition: 0.3s ease-in-out;
	-moz-transition: 0.3s ease-in-out;
	-o-transition: 0.3s ease-in-out;
	transition: 0.3s ease-in-out;
}

.image-selector.hide {
	max-height: 0em;
	padding: 0;
	visibility: hidden;
}

.image-selector-preview {
	max-height: 100vh;
	overflow-y: hidden;
	-webkit-transition: 0.6s ease-in-out;
	-ms-transition: 0.6s ease-in-out;
	-moz-transition: 0.6s ease-in-out;
	-o-transition: 0.6s ease-in-out;
	transition: 0.6s ease-in-out;
}

.image-selector-preview.hide {
	max-height: 0em;
	padding: 0;
}

.image-selector-preview.hide .ghost-button {
	border: none;
}

.image-selector-preview img {
	width: 60%;
}

.image-to-add {
	cursor: move;
	cursor: grab;
}

.wall {
	width: 1920px;
	min-height: 1080px;
	overflow-y: hidden;
	position: absolute;
	top: 0;
	left: 0;
	opacity: 1;
	visibility: visible;
	-webkit-transition: 0.6s ease-in-out;
	-ms-transition: 0.6s ease-in-out;
	-moz-transition: 0.6s ease-in-out;
	-o-transition: 0.6s ease-in-out;
	transition: 0.6s ease-in-out;
}

#wall-front {
	background: url(assets/images/room-front.jpg);
	background-size: contain;
}

#wall-right {
	background: url(assets/images/room-right.jpg);
	background-size: contain;
}

#wall-back {
	background: url(assets/images/room-back.jpg);
	background-size: contain;
}

#wall-left {
	background: url(assets/images/room-left.jpg);
	background-size: contain;
}

.wall.hide {
	opacity: 0;
	visibility: hidden;
}

.image-to-add-text-inputs {
	text-align: right;
	width: 280px;
	margin: 1em auto;
}

.image-to-add-container {
	width: 450px;
	padding: 1em;
	left: 0;
	right: 0;
	margin: auto;
	min-height: 40vh;
	position: fixed;
}

.image-to-add-container.hide {
	top: -100%;
}

.wall-piece {
	position: absolute;
	cursor: pointer;
	opacity: 1;
	visibility: visible;
	-webkit-transition: 0.3s ease-in-out;
	-ms-transition: 0.3s ease-in-out;
	-moz-transition: 0.3s ease-in-out;
	-o-transition: 0.3s ease-in-out;
	transition: 0.3s ease-in-out;
}

.wall-piece.hide {
	opacity: 0;
	visibility: hidden;
}

.wall-piece:hover {
	box-shadow: 7px 7px 3px 1px rgba(0, 0, 0, 0.3);
	transform: scale(1.05);
}

#input-image-file-label {
	width: 15em;
}

#input-image-file-label svg {
	fill: rgba(255, 255, 255, 0.7);
	width: 24px;
	height: 14.4px;
	-webkit-transition: 0.3s ease-in-out;
	-ms-transition: 0.3s ease-in-out;
	-moz-transition: 0.3s ease-in-out;
	-o-transition: 0.3s ease-in-out;
	transition: 0.3s ease-in-out;
}

#input-image-file-label:hover svg {
	fill: rgba(255, 255, 255, 1);
}

#input-image-url {
	width: 15em;
	height: 3em;
	margin: auto;
}

#input-image-file {
	z-index: -100;
	position: fixed;
	opacity: 0;
}

.image-inputs {
	text-align: center;
	display: grid;
	grid-template-columns: 1fr 1fr;
}

.ghost-button {
	border: 3px solid #ffffff;
	opacity: 0.7;
	border-radius: 5px;
	cursor: pointer;
	margin: 0.5em auto;
	padding: 0.5em;
	display: inline-block;
	-webkit-transition: 0.3s ease-in-out;
	-ms-transition: 0.3s ease-in-out;
	-moz-transition: 0.3s ease-in-out;
	-o-transition: 0.3s ease-in-out;
	transition: 0.3s ease-in-out;
}

.ghost-button:hover {
	opacity: 1;	
}

.wall-piece-info-container, .force-login-container, .buffering-container, .error-window-container {
	background-color: rgba(238, 238, 238, 0.6);
	width: 100%;
	height: 100%;
	position: fixed;
	top: 0;
	overflow: hidden;
	-webkit-transition: 0.3s ease-in-out;
	-ms-transition: 0.3s ease-in-out;
	-moz-transition: 0.3s ease-in-out;
	-o-transition: 0.3s ease-in-out;
	transition: 0.3s ease-in-out;
}

.wall-piece-info-grid-container {
	display: grid;
	grid-template-columns: 1fr 1fr;
	height: 100%;
}

.wall-piece-info-image-container {
	margin: 2em;
	width: 100%;
}

.wall-piece-info-image {
	max-width: 100%;
	max-height: 100%;
}

.wall-piece-info {
	text-align: left;
	padding: 2em 5em;
	font-weight: 700;
}

.wall-piece-info span.heading {
	font-size: 4rem;

}
.wall-piece-info span.subheading {
	font-size: 2rem;
}

.wall-piece-info p {
	font-size: 24px;
}

.wall-piece-info svg {
	fill: #ffffff;
	stroke-width: 0.5;
	width: 48px;
	height: 48px;
	cursor: pointer;
	-webkit-transition: 0.1s ease-in-out;
	-ms-transition: 0.1s ease-in-out;
	-moz-transition: 0.1s ease-in-out;
	-o-transition: 0.1s ease-in-out;
	transition: 0.1s ease-in-out;
}

.wall-piece-info svg:hover, .wall-piece-info svg.active {
	fill: #ff0000;
	stroke: none;
	transform: scale(1.1);
}

.likes-counter {
	margin-left: 0.25em;
	vertical-align: bottom;
	font-size: 3rem;
}

.wall-piece-info-container .close-window-button, .force-login-container .close-window-button, .error-window-container .close-window-button {
	color: #212121;
	top: 0.5rem;
}

.wall-piece-info-container.hide, .force-login-container.hide, .error-window-container.hide, .wall-piece-info-container.hide .close-window-button, .force-login-container.hide .close-window-button, .error-window-container.hide .close-window-button, .error-window-container.hide span.heading {
	top: -100%;
}

.buffering-container span.heading, .error-window-container span.heading {
	width: 50%;
	height: 20%;
	position: fixed;
	left: 0;
	right: 0;
	top: 0;
	bottom: 0;
	margin: auto;
}

.error-window-container span.heading {
	-webkit-transition: 0.3s ease-in-out;
	-ms-transition: 0.3s ease-in-out;
	-moz-transition: 0.3s ease-in-out;
	-o-transition: 0.3s ease-in-out;
	transition: 0.3s ease-in-out;
}

input[type = range] {
	-webkit-appearance: none;
	margin: 18px 0;
	width: 70%;
}

input[type = range]:focus {
	outline: none;
}

input[type = range]::-webkit-slider-runnable-track {
	width: 70%;
	height: 6px;
	cursor: pointer;
	animate: 0.2s;
	background: #ffffff;
	border-radius: 1.3px;
}

input[type = range]::-webkit-slider-thumb {
	height: 33px;
	width: 12px;
	border-radius: 3px;
	background: #f50057;
	cursor: pointer;
	-webkit-appearance: none;
	margin-top: -13px;
}

input[type = range]:focus::-webkit-slider-runnable-track {
	background: #ffffff;
}

input[type = range]::-moz-range-track {
	width: 70%;
	height: 6px;
	cursor: pointer;
	animate: 0.2s;
	background: #ffffff;
	border-radius: 1.3px;
}

input[type = range]::-moz-range-thumb {
	height: 33px;
	width: 12px;
	border-radius: 3px;
	background: #f50057;
	cursor: pointer;
}

input[type = range]::-ms-track {
	width: 70%;
	height: 6px;
	cursor: pointer;
	animate: 0.2s;
	background: transparent;
	border-color: transparent;
	color: transparent;
}

input[type=range]::-ms-thumb {
	height: 33px;
	width: 12px;
	border-radius: 3px;
	background: #f50057;
	cursor: pointer;
}

@media only screen and (max-width: 900px) {
	html {
		font-size: 80%;
	}

	.image-inputs {
		grid-template-columns: 1fr;
	}
}

@media only screen and (max-width: 600px) {
	html {
		font-size: 60%;
	}

	.image-inputs {
		grid-template-columns: 1fr;
	}
}